/* @import 'normalize.css'; */

:root {
    --white-color: #ffffff;
    --yellow-color: #eac645;
    --violet-color: #4f2ee8;
    --black-first: #202024;
    --black-second: #111111;
  }

  body {
    font-family: 'Orbitron', sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: rgba(255, 255, 255, 1);

    height: 100vh; /* Задаем высоту секции на весь экран */
    background: linear-gradient(to right, rgba(0, 0, 0, 0.88) 60%,  rgba(87, 184, 255, 1) 40%);
  }
  
  h1, h2, h3, h4, h5, h6 {
    margin: 0;
  }
  
  a {
    text-decoration: none;
    color: inherit;
  }


h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}


ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button{
  all:unset;
}